<template>
  <div>
    <button @click="fetchData">get data</button>
  </div>
  <br /><br /><br />
  <div>{{ users }}</div>
</template>

<script>

//axios 라이브러리 import
import axios from 'axios'

export default {
  name: 'ProfileView',
  data() {
    return {
      users: [],
    };
  },
  methods: {
    fetchData() {
      const vm = this;
      axios
          .get('http://localhost:8080/api/')
          .then(response => {
            console.log(response);
            vm.users = response.data;
          })
          .catch(err => {
            console.log(err);
          })
    }
  }
};
</script>

<style scoped></style>